import styled from 'styled-components'

export const ComunityContainer = styled.div`
    width: 100%;
    margin: 20px 0;
`;

export const MembersRow = styled.div`
    display: flex;
    width: 100%;
    padding: 2px 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
`;

export const MemberProfilePic = styled.img`
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin: 5px 10px;
`;