import React, { Component } from 'react';
import { Collapse } from 'reactstrap';

export default class Collapsable extends Component {
    state={
        open: false
    }

    render() {
        const { open } = this.state
        return (
            <div style={{width: '100%', border: '1px solid grey', borderRadius: '5px', padding: '.55em .5em'}}>
                <div style={{display: 'flex', alignItems: 'center'}} onClick={() => this.setState({open: !open})} >
                    <span>{ this.props.title }</span>
                    <div style={{display: 'inline-block', flex: 1}} />
                    <span style={{cursor: 'pointer'}}>v</span>
                </div>
                <Collapse isOpen={open} >
                    { this.props.children }
                </Collapse>
            </div>
        )
    }
}