import React, { Component, Fragment } from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, FormGroup, Label, Input } from 'reactstrap';
import fraternization from '../../assets/meeting.png'
import integration from '../../assets/solution.png'
import { connect } from 'react-redux'
import { closeModal } from './actions'
import moment from 'moment';

class EventModal extends Component {
    state = {
        eventType: null,
        hobbieTags: [],
        tags: [],
        title: '',
        date: '',
        time: '',
        location: '',
        description: ''
    }

    async componentDidMount() {
        const events = await fetch('http://mojo.ejcm.com.br/api/tags', {method: 'GET'})
            .then(res => res.json())
            .catch(err => console.log(err))

        console.log(events)
        this.setState({tags: events ? events.data : [] })
    }

    close() {
        this.props.closeModal()
    }

    changeContent(eventType) {
        this.setState({eventType})
    }

    handleTag(tag, category) {
        const state = this.state

        if (state[category].includes(tag)) {
            this.setState({[category]: state[category].filter(item => item !== tag)})
        } else {
            this.setState({[category]: [...state[category], tag]})
        }
    }

    async handleSubmit() {
        const { hobbieTags, tags, title, date, time, location, description, eventType } = this.state
        console.log(date, time)
        const events = await fetch('http://mojo.ejcm.com.br/api/events',{method: 'POST', body: JSON.stringify({tags: hobbieTags, title, dateTime: moment(date + ' ' + time).format('DD/MM/YYYY HH:MM'), location, description, type: eventType})})
            .then(res => res.json())
            .catch(err => console.log(err))

        console.log(events)
    }

    render(){
        const { isOpen, user } = this.props
        const { eventType, hobbieTags, tags } = this.state
        return (
            <Modal isOpen={ isOpen } >
                <ModalHeader>
                    {eventType
                        ?
                            <h3>{eventType.toUpperCase()}</h3>
                        :
                            <span>Hi {user.name}, what are we going to create today?</span>
                    }
                </ModalHeader>
                <ModalBody>
                    {eventType
                        ?
                            <div>
                                <input onChange={(e) => this.setState({title: e.target.value})} style={{width: '100%', marginBottom: '20px'}} placeholder='Event Title' type='text' />
                                <input onChange={(e) => this.setState({date: e.target.value})} style={{width: '40%', marginBottom: '20px'}} placeholder='Event Date' type='date' />
                                <input onChange={(e) => this.setState({time: e.target.value})} style={{width: '20%', marginLeft: '20px'}} placeholder='Event Time' type='time' />
                                <input onChange={(e) => this.setState({location: e.target.value})} style={{width: '100%', marginBottom: '20px'}} placeholder='Address' type='text' />
                                <textarea style={{width: '100%', marginBottom: '20px'}} placeholder='Description' onChange={(e) => this.setState({description: e.target.value})}/>
                                <div style={{display: 'flex', flexWrap: 'wrap'}}>
                                    {tags.map(hobbie =>
                                        <div onClick={() => this.handleTag(hobbie.name, 'hobbieTags')} style={{border: '1px solid lightblue', backgroundColor: hobbieTags.includes(hobbie.name) ? 'blue' : 'white', display: 'flex', alignItems: 'center', justifyContent: 'center', margin: '10px', padding: '6px', borderRadius: '15px', cursor: 'pointer'}}>
                                            {hobbie.name}
                                        </div>
                                    )}
                                </div>
                            </div>
                        :
                            <div style={{minHeight: '200px', display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}} >
                                <div onClick={() => this.changeContent('fraternization')} style={{display: 'flex', width: '200px', height: '200px', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', borderRadius: '50%', backgroundColor: '#ffd204'}} >
                                    <img style={{width: '40%'}} src={fraternization} alt='' />
                                    Fraternization
                                </div>
                                <div onClick={() => this.changeContent('integration')} style={{display: 'flex', width: '200px', height: '200px', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', borderRadius: '50%', backgroundColor: '#ffd204'}} >
                                    <img style={{width: '40%'}} src={integration} alt='' />
                                    Integration
                                </div>
                            </div>
                    }
                </ModalBody>
                {eventType &&
                    <ModalFooter>
                        <Fragment>
                            <Button color='primary' onClick={() => this.handleSubmit()}>Submit</Button>
                            <Button color='secondary' onClick={() => this.close()}>Cancel</Button>
                        </Fragment> 
                    </ModalFooter>
                }
            </Modal>
        )
    }
}

function MapStateToProps(state, ownProps) {
    return {
        ...ownProps,
        event: state.evaluationModal.evaluatedEvent,
        user: state.userReducer.user,
    }
}

export default connect(MapStateToProps)(EventModal)

const mockedHobbies = ['Esportes', 'Esportes Radicais', 'Msica', 'Arte', 'Ao Social', 'Lazer', 'Sade & Bem Estar', 'Gastronomia', 'Religio']