import React, { Component } from 'react';
import { FullSizeContainer } from '../common/Styled';
import Collapsable from '../common/Collapsable';
import { Row, Col } from 'reactstrap';
import { connect } from 'react-redux'

class Profile extends Component {
    state = {
        hobbieTags: mock.hobbieTags,
        techTags: mock.techTags
    }

    handleTag(tag, category) {
        const state = this.state

        if (state[category].includes(tag)) {
            this.setState({[category]: state[category].filter(item => item !== tag)})
        } else {
            this.setState({[category]: [...state[category], tag]})
        }
    }

    render() {
        const { user } = this.props
        const { hobbieTags, techTags } = this.state
        return (
            <FullSizeContainer>
                <Row>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Full Name</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.name } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Year of Admission</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.admission } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Birthday</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.birthday } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Area of Actuation</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.occupation } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Email</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.email } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Education</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.occupation } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Sector</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.sector } />
                    </Col>
                    <Col md='6' sm='12'>
                        <p style={{marginBottom: 0}}>Education Institute</p>
                        <input style={{ marginBottom: '20px', width: '100%' }} type='text' disabled value={ user.institute } />
                    </Col>
                    <Col md='6' sm='12'>
                        <Collapsable title="Interesses">
                            <div style={{display: 'flex', flexWrap: 'wrap'}} >
                                {mockedHobbies.map(hobbie => 
                                    <div onClick={() => this.handleTag(hobbie, 'hobbieTags')} style={{border: '1px solid lightblue', backgroundColor: hobbieTags.includes(hobbie) ? 'blue' : 'white', display: 'flex', alignItems: 'center', justifyContent: 'center', margin: '10px', padding: '6px', borderRadius: '15px', cursor: 'pointer'}}>
                                        {hobbie}
                                    </div>
                                )}
                            </div>
                        </Collapsable>
                    </Col>
                    <Col md='6' sm='12'>
                        <Collapsable title="Habilidades">
                            <div style={{display: 'flex', flexWrap: 'wrap'}} >
                                {mockedInterpessonal.map(hobbie => 
                                    <div onClick={() => this.handleTag(hobbie, 'techTags')} style={{border: '1px solid lightblue', backgroundColor: techTags.includes(hobbie) ? 'blue' : 'white', display: 'flex', alignItems: 'center', justifyContent: 'center', margin: '10px', padding: '6px', borderRadius: '15px', cursor: 'pointer'}}>
                                        {hobbie}
                                    </div>
                                )}
                            </div>
                        </Collapsable>
                    </Col>
                </Row>
            </FullSizeContainer>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.userReducer.user
    }
}

export default connect(mapStateToProps)(Profile)

const mockedHobbies = ['Esportes', 'Esportes Radicais', 'Msica', 'Arte', 'Ao Social', 'Lazer', 'Sade & Bem Estar', 'Gastronomia', 'Religio']
const mockedInterpessonal = ['Trabalho em equipe', 'Liderana', 'Gesto', 'Comunicao', 'Idiomas', 'Resoluo de Problemas', 'Persuaso', 'Criatividade', 'Disciplina', 'Proatividade', 'Negcios']
const mockedTech = ['Programao', 'Design', 'Oratria', 'Idioma', 'Marketing', 'Comercial', 'Networking']

const mock = {
    name: 'Gustavo Henrique Areias',
    admissionYear: 2015,
    birthday: '15/07/1996',
    areaOfActuation: 'Oil Enginneer',
    email: 'gustavoha@email.com',
    graduation: 'engineer',
    sector: 'oil',
    institution: 'Universidade Federal Fluminense',
    hobbieTags: [
        'Esportes',
        'Msica',
        'Arte',
        'Cinema',
        'Flamengo'
    ],
    techTags: [
        'Idiomas',
        'Liderana',
        'Sales',
        'Excel',
    ],
}