import React, { Component } from 'react'
import { WholePageContainer } from '../common/Styled';
import { LoginContainer, UserAvatar, InputIcon, LoginButton, LoginText } from './Styled';
import { InputGroup, Input, InputGroupAddon } from 'reactstrap'
import avatar from '../../assets/avatar.png'
import redStroke from '../../assets/red-brush-stroke.png'
import ShellLogo from '../../assets/Shell_logo.svg'

export default class Login extends Component {
    state = {
        email: '',
        password: '',
        error: false
    }

    loginHandler() {
        const { email, password } = this.state
        if (email && password) {
            alert('logou com sucesso')
        } else {
            this.setState({error: true})
        }
    }
    
    render(){
        const { error, email, password } = this.state
        const errorEmail = (error && !email) ? true : false
        const errorPassword = (error && !password) ? true : false

        return (
            <WholePageContainer>
                <LoginContainer>
                    <UserAvatar src={avatar} />
                    <InputGroup>
                        <InputGroupAddon addonType='prepend'>
                            <InputIcon src={avatar}/>
                        </InputGroupAddon>
                        <Input style={{height: '100%', borderColor: errorEmail ? 'red' : ''}} id='email' type='email' placeholder='Login' onChange={(e) => this.setState({email: e.target.value})} />
                    </InputGroup>
                    <InputGroup>
                        <InputGroupAddon addonType='prepend'>
                            <InputIcon src={avatar}/>
                        </InputGroupAddon>
                        <Input style={{height: '100%', borderColor: errorPassword ? 'red' : ''}} id='email' type='email' placeholder='Password' onChange={(e) => this.setState({password: e.target.value})} />
                    </InputGroup>
                    <LoginButton style={{outline: 'none'}} onClick={() => this.loginHandler()}>
                        <img style={{height: '60px'}} src={redStroke} alt='' />
                        <LoginText>LOGIN</LoginText>
                    </LoginButton>
                </LoginContainer>
                <img style={{height: '126px', width: '136px'}} src={ShellLogo} alt='' />
            </WholePageContainer>
        )
    }
}