import React from 'react';
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom'
import { basicRoutes } from './utils/router/RouterConfig'
import { createStore, combineReducers } from 'redux'
import { reducer as formReducer } from 'redux-form'
import { Provider } from 'react-redux'
import Reactotron from './config/Reactotron'
import { evaluationModal } from './components/evaluation_modal/reducers';
import { userReducer } from './components/common/duck/userDuck';
import { createGlobalStyle } from 'styled-components'
import alegreya from './assets/fonts/AlegreyaSans-Regular.otf'

const rootReducer = combineReducers({
  form: formReducer,
  evaluationModal: evaluationModal,
  userReducer,
})

const store = createStore(rootReducer, Reactotron.createEnhancer())

const GlobalStyle = createGlobalStyle`
  @font-face {
     font-family: alegreya;
     src: url('./assets/fonts/AlegreyaSans-Regular.otf');
  }

  body {
    font-family: alegreya;
  }
`

function App() {
  return (
    <Provider store={ store } >
      <GlobalStyle />
      <BrowserRouter>
        <Switch>
          <Route exact path='/' render={props => <Redirect to={{pathname: '/experience'}} />} />
          {basicRoutes}
        </Switch>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
