import React from 'react'
import { menuItems, baseRoutes } from '../constants/routes'
import { Route } from 'react-router-dom'

export const protectedRoutes = menuItems.map((item, index) => <Route key={ item.name + index } path={ item.path } component={ item.component } />)

export const basicRoutes = baseRoutes.map((item, index) => <Route key={ item.name + index } path={ item.path } component={ item.component } />)