import React, { Component } from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, FormGroup, Label, Input } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar } from '@fortawesome/free-solid-svg-icons'
import { faStar as emptyStar } from '@fortawesome/free-regular-svg-icons'
import { connect } from 'react-redux'
import { closeModal } from './actions'

class EvaluationModal extends Component {
    state = {
        wasThere: null,
        eventScore: 0, 
        sent: null
    }

    close() {
        this.props.dispatch(closeModal())
    }

    handleSubmit() {
        const { sent, eventScore } = this.state

        if (sent) {
            this.close()
        } else if (eventScore) {
            this.setState({sent: true})
        } else {
            alert('You need to give the event a score before submitting')
        }
    }

    render(){
        const { isOpen, evaluatedEvent } = this.props
        const { wasThere, eventScore, sent } = this.state
        return (
            <Modal isOpen={ isOpen } >
                <ModalHeader>
                    {sent 
                        ?
                            <span>Parabens voc ganhou <strong>500 Joules</strong></span>
                        :
                            `E ai o que achou de ${evaluatedEvent && evaluatedEvent.name}?`
                    }
                </ModalHeader>
                <ModalBody>
                    {/* <div>
                        <FormGroup tag="fieldset">
                            <legend>Voc foi?</legend>
                            <div>
                                <FormGroup check>
                                    <Label check>
                                        <Input onClick={() => this.setState({wasThere: true})} type="radio" name="radio1" />{' '}
                                        Sim, eu tava la
                                    </Label>
                                </FormGroup>
                                <FormGroup check>
                                    <Label check>
                                        <Input onClick={() => this.setState({wasThere: false})} type="radio" name="radio1" />{' '}
                                        No, infelizmente eu perdi essa
                                    </Label>
                                </FormGroup>
                            </div>
                        </FormGroup>
                    </div> */}
                    {!sent 
                        ?
                            <div>
                                <legend>Conta para a gente como  que foi</legend>
                                <div>
                                    {stars.map(star => {
                                        if (star > eventScore) {
                                            return <FontAwesomeIcon onClick={() => this.setState({eventScore: star})} color='black'  icon={emptyStar} />
                                        } else {
                                            return <FontAwesomeIcon onClick={() => this.setState({eventScore: star})} color='yellow' icon={faStar} />
                                        }
                                    })}
                                </div>
                            </div>
                        :
                            <div>
                                <img style={{ width: '100%'}} src='https://media.giphy.com/media/26tOZ42Mg6pbTUPHW/giphy.gif' alt='explosion' />
                            </div>
                    }
                </ModalBody>
                <ModalFooter>
                    <Button color='primary' onClick={() => this.handleSubmit()}>{ sent ? 'Fechar' : 'Enviar' }</Button>
                    {!sent &&
                        <Button color='secondary' onClick={() => this.close()}>No Fui</Button>
                    }
                </ModalFooter>
            </Modal>
        )
    }
}

function MapStateToProps(state, ownProps) {
    return {
        ...ownProps,
        isOpen: state.evaluationModal.isOpen,
        event: state.evaluationModal.evaluatedEvent,
    }
}

export default connect(MapStateToProps)(EvaluationModal)

const stars = [1, 2, 3, 4, 5]