import React from 'react';
import { MembersRow, MemberProfilePic } from '../Styled';

const ComunityRow = ({comunityMembers}) => {
    return (
        <MembersRow>
            {comunityMembers.map(member => 
                <MemberProfilePic src={member.memberPicture}/>
            )}
        </MembersRow>
    )
}

export default ComunityRow
