import styled from 'styled-components'

export const BalancePageContainer = styled.div`
    width: 80%;
    height: 100%;
    text-align: center;

    @media (max-width: 650px) {
        width: 100%;
    }
`;

export const BalanceWrapper = styled.div`
    width: 30vw;
    height: 10vw;
    border: 1px solid black;
    border-radius: 10px;
    margin-bottom: 30px;
    position: relative;

    >div {
        height: 100%;
    }

    @media (max-width: 600px) {
        width: unset;
        height: unset;
        min-width: 70vw;
        min-height: 30vw;

        h4 {
            font-size: 1.2em;
        }

        span, p {
            font-size: 15px;
        }
    }

    ::after {
        content: '';
        top: calc(50% - 10px);
        right: -11px;
        border: 2px solid gray;
        border-left: none;
        width: 10px;
        height: 20px;
        position: absolute;
    }
`;