const GET_USER = 'GET_USER'

export function getUser(data) {
    return ({
        type: GET_USER,
        data
    })
}

export function userReducer(state = {user: {}}, action) {
    switch(action.type) {
        case GET_USER:
            return {
                ...state,
                user: action.data
            }
        default:
            return {
                ...state
            }
    }
}