import styled from 'styled-components';

export const EventContainer = styled.div`
    width: 100%;
    min-height: 25vh;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-bottom: 30px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;

    div:nth-child(1) {
        position: absolute;
        right: 2px;
        top: 2px;
        overflow-x: unset;

        div {
            height: 30px;
            width: 30px;
            border: 2px solid black;
            border-radius: 5px;
            margin-bottom: 5px;
            background-color: white;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                height: 80%;
            }
        }
    }
    
    div:nth-child(2) {
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: rgba(233, 233, 233, 0.5);
        width: 100%;

        p {
            margin-bottom: 0;
            font-size: .75em;
        }
    }
`;