import React, {Component, Fragment} from 'react';
import { Row, Col } from 'reactstrap';
import { BalanceWrapper, BalancePageContainer } from './Styled';
import GreenEnergy from '../../assets/green-energy.png'
import redEnergy from '../../assets/red-energy.png'

export default class Experience extends Component {
    state = {
        confirmed: {}
    }
    render() {
        return (
            <BalancePageContainer>
                {mockup.map((item, index) =>
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <BalanceWrapper>
                            <Row>
                                <Col xs='3' sm='3' md='3' lg='3'>
                                    <div style={{height: '100%', width: '100%', backgroundColor: '#b6b6b6', borderTopLeftRadius: '10px', borderBottomLeftRadius: '10px', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                        <img style={{width: '60%'}} src={ item.type === '-' ? redEnergy : GreenEnergy } alt='' />
                                    </div>
                                </Col>
                                <Col style={{padding: 0}} xs='6' sm='6' md='6' lg='6'>
                                    <div style={{width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center'}} >
                                        <h4>
                                            {item.description}
                                        </h4>
                                    </div>
                                    <span style={{position: 'absolute', bottom: 0, left: 10}}>{item.date}</span>
                                    <span style={{position: 'absolute', bottom: 0, right: 10}}>{item.time}</span>
                                </Col>
                                <Col xs='3' sm='3' md='3' lg='3'>
                                    <div style={{height: '100%', width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center'}} >
                                        <h4 style={{color: item.type === '-' ? 'red' : 'green', textAlign: "center"}} >
                                            {item.type + item.value}
                                        </h4>
                                    </div>
                                </Col>
                            </Row>
                        </BalanceWrapper>
                    </div>
                )}
            </BalancePageContainer>
        )
    }
}

