import React from 'react';
import { Row, Col } from 'reactstrap';
import avatar from '../../assets/Ale2.jpg'
import ShellLogo from '../../assets/Shell_logo.svg'
import { HeaderContainer, ProfilePicContainer, CompanyLogo, FlexContainer } from './Styled';
import { menuItems } from '../../utils/constants/routes'
import { withRouter } from "react-router";

const Header = (props) => {
    const width = window.innerWidth
    const currentScreen = menuItems.find(item => item.path === props.location.pathname) || {}
    return (
        <HeaderContainer>
            <Row>
                { width > 700 &&
                    <Col md='3' lg='2'>
                        <ProfilePicContainer>
                            <img src={ avatar } alt=''/>
                        </ProfilePicContainer>
                        <p style={{textAlign: 'center', marginBottom: 0}} >Hi, {/*props.userName*/}Al!</p>
                    </Col>
                }
                <Col xs='8' md='6' lg='8'>
                    { width > 700
                        ?
                            <div>
                                <h2>{currentScreen.name}</h2>
                            </div>
                        :
                            <select onChange={(e) => {console.log(props); return props.history.push(e.target.value)}} value={currentScreen.path} >
                                {menuItems.map(item => 
                                    <option key={ item.path } value={ item.path }>{item.name}</option>
                                )}
                            </select>
                    }
                </Col>
                <Col xs='4' md='3' lg='2'>
                    <FlexContainer justify='space-evenly' >
                        <CompanyLogo src={ShellLogo} alt=''/>
                        <span style={{fontSize: width < 700 ? '10px' : ''}} ></span>
                    </FlexContainer>
                </Col>
            </Row>
        </HeaderContainer>
    )
}

export default withRouter(Header)