import React, { Component } from 'react';
import { WholePageContainer } from './Styled';
import Header from './Header';
import Body from './Body';
import EvaluationModal from '../evaluation_modal';
import { getUser } from './duck/userDuck'
import { connect } from 'react-redux'

class BaseTemplate extends Component{

    async componentDidMount() {
        const user = await fetch('http://mojo.ejcm.com.br/api/users',{method: 'GET'})
            .then(res => res.json())
            .catch(err => console.log(err))
    
        console.log(user)
        this.props.dispatch(getUser(user.data[0]))
    }

    render(){
        const { user } = this.props
        console.log(user)
        return (
            <WholePageContainer justifyContent='flex-start'>
                <Header userName={ user ? user.name : '' } />
                <Body user={ user ? user : {} } />
                <EvaluationModal isOpen={ true } evaluatedEvent={{name: 'Caf Desafio', id: 2}} />
            </WholePageContainer>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.userReducer.user
    }
}

export default connect(mapStateToProps)(BaseTemplate)