import React, {Component} from 'react';
import { Row, Col } from 'reactstrap';
import { EventContainer } from './styled'
import Check from '../../assets/tick-inside-circle.png'
import UnCheck from '../../assets/gray-tick-circle.png'
import event1 from '../../assets/1.png'
import event2 from '../../assets/2.png'
import event3 from '../../assets/3.png'
import event4 from '../../assets/4.png'
import event5 from '../../assets/5.png'
import event6 from '../../assets/6.png'
import event7 from '../../assets/7.png'
import event8 from '../../assets/8.png'
import event9 from '../../assets/9.png'
import { openModal } from '../evaluation_modal/actions';
import EventModal from '../event_modal';

export default class Experience extends Component {
    constructor(props){
        super(props)

        this.state = {
            confirmed: [],
            events: [],
            modalOpen: false
        }
    }
    
    async componentDidMount() {
        const events = await fetch('http://mojo.ejcm.com.br/api/home/1',{method: 'GET'})
            .then(res => res.json())
            .catch(err => console.log(err))

        console.log(events)
        this.setState({events: events ? events.data.eventsForHome : [], confirmed: events ? events.data.eventsconfirmed : []})
        if (events && events.data.eventsForPopUP && events.data.eventsForPopUP.length) {
            this.props.dispatch(openModal(events.data.eventsForPopUP))
        }
    }

    getImage(imageId) {
        switch(imageId){
            case '1':
                return event1
            case '2':
                return event2
            case '3':
                return event3
            case '4':
                return event4
            case '5':
                return event5
            case '6':
                return event6
            case '7':
                return event7
            case '8':
                return event8
            case '9':
                return event9
            default:
                return event1
        }
    }

    confirm(id) {
        const events = fetch('http://mojo.ejcm.com.br/api/participation',{method: 'POST', body: JSON.stringify({event_id: id, user_id: '1', confirm_status: true})})
            .then(res => res.json())
            .catch(err => console.log(err))
    }

    render() {
        const { events } = this.state
        console.log(events)
        return (
            <Row>
                {events.map((event, index) => 
                    <Col key={ event.start_time + index } xs='12' sm='12' md='6' lg='4'>
                        <EventContainer style={{backgroundImage: `url(${this.getImage(event.img)})`}}>
                            <div>
                                <div style={{backgroundColor: this.state.confirmed.includes(event.id) ? '' : '#dbdbdb' }} onClick={() => this.confirm(event.id)} >
                                    <img src={ this.state.confirmed.includes(event.id) ? Check : UnCheck } alt='' />
                                </div>
                                {/* <div style={{height: '30px', width: '30px', border: '2px solid black', borderRadius: '5px'}}>

                                </div> */}
                            </div>
                            <div>
                                <p>{event.title ? event.title + ' |' : ''} {event.start_time ? event.start_time + ' |' : ''} {event.location}</p>
                            </div>
                        </EventContainer>
                    </Col>
                )}
                <EventModal closeModal={() => this.setState({modalOpen: false})} isOpen={ this.state.modalOpen } />
            </Row>
        )
    }
}

