import styled from 'styled-components';

export const WholePageContainer = styled.div`
    width: 99vw;
    height: 100vh;
    display: flex;
    flex-direction: ${props => props.direction || 'column'};
    align-items: ${props => props.alignItems || 'center'};
    justify-content: ${props => props.justifyContent || 'center'};
    background: ${props => props.background || '#f6f6f6'};
    font-family: alegreya;
`;

export const FullSizeContainer = styled.div`
    width: 100%;
    height: 100%;
`;

export const StepArrow = styled.div`
    width: 120px;
    height: 50px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    ::before {
        left: 0px;
        top: 0px;
        width: 100%;
        height: 50%;
        transform: skew(45deg);
        position: absolute;
        border: ${props => props.current ? `5px solid ${props.selectedColor || 'red'}` : '5px solid lightgray'};
        border-left: ${props => props.current ? `7px solid ${props.selectedColor || 'red'}` : '7px solid lightgray'};
        border-right: ${props => props.current ? `7px solid ${props.selectedColor || 'red'}` : '7px solid lightgray'};
        border-bottom: none;
        content: '';
    }
    ::after {
        left: 0px;
        bottom: 1px;
        width: 100%;
        height: 50%;
        transform: skew(-45deg);
        position: absolute;
        border: ${props => props.current ? `5px solid ${props.selectedColor || 'red'}` : '5px solid lightgray'};
        border-left: ${props => props.current ? `7px solid ${props.selectedColor || 'red'}` : '7px solid lightgray'};
        border-right: ${props => props.current ? `7px solid ${props.selectedColor || 'red'}` : '7px solid lightgray'};
        border-top: none;
        content: '';
    }
`;

export const HeaderContainer = styled.div`
    width: 100%; 
    padding: 1em 2em;

    h2 {
        font-size: 4rem;
        text-align: left;
    }
    @media (max-width: 1000px) {
        h2 {
            font-size: 3rem;
        }
    }
    @media (max-width: 600px) {
        position: fixed;
        z-index: 999;
        background-color: white;
        height: 15vh;
        padding: 1em 0;
        select {
            font-size: 2rem;
            border: none;
            outline: none;
            background-color: inherit;
        }
    }
`;

export const BodyContainer = styled.div`
    width: 100%;
    height: 70vh;
    padding: 0 2em;

    div {
        max-height: 70vh;
    }

    @media (max-width: 600px) {
        height: auto;
        div {
            max-height: 85vh;
        }
        padding: 0 .5em;
        margin-top: 15vh;
    }
`;

export const ProfilePicContainer = styled.div`
    // keeps proportion 1:1
    padding-top: 70%;  
    width: 70%;
    background-color: #ffd204;
    border-radius: 50%;
    position: relative;
    margin-left: 15%;
    img {
        position: absolute;
        width: 90%;
        height: 90%;
        border-radius: 50%;
        top: 5%;
        left: 5%;
        background-color: white;
    }
`;

export const FlexContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: ${props => props.direction || 'column'};
    justify-content: ${props => props.justify || 'center'};
    align-items: ${props => props.alignItems || 'center'};
`;

export const CompanyLogo = styled.img`
    width: 30%;
    display: block;

    @media (max-width: 700px) {
        width: 50%;
    }
`;

export const Menu = styled.div`
    width: 85%;
    background-color: #d9d9d9;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    padding: 1.5em .5em;
    text-align: center;

    a {
        background-color: #625b67;
        margin-bottom: 5px;
        color: #fff;
        border-radius: 5px;
    }

    @media (max-width: 1200px) {
        width: 100%;
    }
`;

export const JouleContainer = styled.div`
    margin-top: 20px;
    border-radius: 20px;
    background-color: #d9d9d9;
    width: 85%;
    padding: 3px 0;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
        font-weight: 800;
    }

    img {
        margin-right: 2px;
        width: 20%;
    }

    @media (max-width: 1200px) {
        width: 100%;

        img {
            width: 20%;
        }

        span {
            font-size: .8rem;
        }
    }
`;