import { OPEN_MODAL, CLOSE_MODAL } from '../actions'

export function evaluationModal(state = {isOpen: true, evaluatedEvent: {}}, action) {
    switch(action.type) {
        case OPEN_MODAL:
            return {
                ...state,
                isOpen: true,
                evaluatedEvent: action.data,
            }
        case CLOSE_MODAL:
            return {
                ...state,
                isOpen: false
            }
        default:
            return {
                ...state
            }
    }
}