import React, {Component} from 'react';
import { Row, Col } from 'reactstrap';
import { EventContainer } from '../experience/styled'
import JouleIcon from '../../assets/green-energy.png'
import Xian from '../../assets/rewards/Xian.jpg'
import ShellRacing from '../../assets/rewards/ShellRacing.jpeg'
import Camiseta from '../../assets/rewards/Camiseta.jpg'
import Mug from '../../assets/rewards/Mug.jpg'
import KeyChain from '../../assets/rewards/KeyChain.png'
import Dinner from '../../assets/rewards/Dinner.jpg'
import ShellHyundai from '../../assets/rewards/ShellHyundai.jpeg'
import BackPack from '../../assets/rewards/BackPack.jpg'
import MuseuDoAmanha from '../../assets/rewards/MuseuDoAmanha.jpg'
import ShellOpenAir from '../../assets/rewards/ShellOpenAir.png'
import FPSO from '../../assets/rewards/FPSO.jpg'
import FerrariBox from '../../assets/rewards/FerrariBox.jpeg'

export default class Experience extends Component {
    state = {
        confirmed: {}
    }
    render() {
        return (
            <Row>
                {mockup.map((product, index) => 
                    <Col key={ product.price + index } xs='12' sm='12' md='6' lg='4'>
                        <EventContainer style={{backgroundImage: `url(${product.img})`, marginBottom: '10px'}} />
                        <div style={{display: 'flex', alignItems: 'center', flexDirection: 'column'}} >
                            <div style={{backgroundColor: '#cbcbcb', border: '1px black solid', borderRadius: '6px', padding: '4px 7px', minWidth: '50px', marginBottom: '10px', display: 'flex', fontSize: '12px', fontWeight: 800,justifyContent: 'center'}}>{product.name}</div>
                            <div style={{backgroundColor: '#cbcbcb', border: '1px black solid', borderRadius: '10px', padding: '4px 7px', minWidth: '125px', marginBottom: '10px', display: 'flex', fontSize: '14px', fontWeight: 800, justifyContent: 'space-around', alignItems: 'center'}}>
                                <img style={{height: '30px'}} src={ JouleIcon } alt=''/> 
                                <div style={{textAlign: "center"}} >
                                    <p style={{marginBottom: 0}} >{product.price}</p> joules
                                </div>
                            </div>
                        </div>
                    </Col>
                )}
            </Row>
        )
    }
}

const mockup = [
    {name: 'Lunch Xian for two', price: 500, img: Xian},
    {name: 'Class Academia de Pilotos Shell Racing', price: 1000, img: ShellRacing},
    {name: 'T-shirt', price: 80, img: Camiseta},
    {name: 'Shell Ferrari Mug', price: 70, img: Mug},
    {name: 'Key chain', price: 20, img: KeyChain},
    {name: 'Family Dinner', price: 800, img: Dinner},
    {name: 'Miniature Shell Hyundai', price: 250, img: ShellHyundai},
    {name: 'Backpack Ferrari', price: 300, img: BackPack},
    {name: 'Museu do Amanha (4 tickets)', price: 150, img: MuseuDoAmanha},
    {name: 'Shell Open Air', price: 300, img: ShellOpenAir},
    {name: 'Site Tour FPSO Fluminense', price: 1500, img: FPSO},
    {name: 'Visit the Ferrari Box', price: 1200, img: FerrariBox},
]