import React, { Fragment } from 'react';
import JouleIcon from '../../assets/green-energy.png'
import { menuItems } from '../../utils/constants/routes'
import { Menu, JouleContainer } from './Styled';
import { Link } from 'react-router-dom'

const SideContent = (props) => {
    return (
        <Fragment>
            <Menu>
                {menuItems.map(item => 
                    <Link to={ item.path }>{item.name}</Link> 
                )}
            </Menu>
            <JouleContainer>
                <img src={ JouleIcon } alt=''/>
                <span>
                    { props.user.wallet } JOULES 
                </span>
            </JouleContainer>
        </Fragment>
    )
}

export default SideContent